import userInfo from '../../components/user/userInfo.vue';
export default {
	components: {
		userInfo
	},
	data() {
		return {
			bgImg: '../../static/images/pyinit.png',
			notice: '十月1日至十月7日放假,十月1日至十月7日放假,十月1日至十月7日放假,十月1日至十月7日放假',
			audio: '',
			user: {
				realName: '',
				account: '',
				img: ''
			}
		};
	},
	onShow() {
		let _this = this;
		uni.getStorage({
			key: 'userInfo',
			success: function(res) {
				if (res.data) {
					_this.user = res.data;
					// _this.user.img = '../../static/images/homeed.png';
				} else {
					uni.redirectTo({
						url: '../login/login'
					});
				}
			},
			fail: function(err) {
				uni.redirectTo({
					url: '../login/login'
				});
			},
			complete: function(data) {
				// console.log(data);
			}
		});
	},
	methods: {
		login() {
			uni.redirectTo({
				url: '/pages/login/login'
			});
		},
		share(){
			console.log("share")
		}
	}
};
